@import "./../../variables";

.error {
	.contentWrapper {
		margin-top: 40px;
		font-size: 2rem;
		font-weight: 700;

		span {
			margin-left: 10px;
			font-size: 3rem;
		}

		.title {
			font-size: 10rem;
			font-weight: 700;
		}

		.mainPageBtn {
			margin: 40px auto 0 auto;
			display: block;
			text-decoration: none;
			padding: 5px 10px;
			border-radius: 5px;
			box-shadow: $shadow;
			width: fit-content;
			cursor: pointer;
			font-weight: 700;
			background: $mainFontColor;
			color: $secondaryColor;

			&:hover {
				background: $secondaryColor;
				color: $secondaryFontColor;
			}
		}
	}
}
